import React,{useState,useEffect} from 'react'
import placesHelper from '../../helper/places'
import {Footer} from '../footer/Footer'
import {withRouter} from 'react-router-dom'

const Place = (props) => {

    const [places,setPlaces] = useState(placesHelper)
    const [displayPlace,setDisplayPlace] = useState(false)

    useEffect(() => {
        window.scrollTo(0, 0);
        verifyPlaces()
    },[])

    const verifyPlaces = async () => {
       try{
         await filterPlaces()
       }
       catch(e){
          props.history.push('/')
       }
    }

    const filterPlaces = () => {
        return new Promise((resolve,reject) => {
            places.filter((place,index) => {
                if(place.id === parseInt(props.match.params.id)){
                    setDisplayPlace(place)
                    resolve()
                }
                if(index === places.length - 1 && displayPlace === false){
                    reject()
                }
            })
        })
    }

    const background = {
        backgroundImage: `url(${displayPlace.img})`,
        backgroundSize:'cover',
        backgroundPosition:'center',
        backgroundRepeat:'no-repeat'
    }
    
    return(
        <section className ="smooth-transition">
            {
            displayPlace ?
                <React.Fragment>
                    <div className ="m-place-intro" style = {background}>
                        <header className="m-place-main-text slide-right">{displayPlace.name}</header>
                    </div>
                    <div className="m-center">
                        <article className="m-place-article">
                           <header>
                               <h4>{displayPlace.name}</h4>
                            </header>
                            <p>
                                Lorem ipsum at donec risus semper nisi gravida curabitur potenti, aenean fringilla tempus leo platea a suspendisse hac ac bibendum, 
                                quisque vehicula potenti egestas volutpat aptent semper praesent. 
                                et dui eu dolor hendrerit enim nibh ipsum, libero porttitor suspendisse blandit euismod at mauris, eget scelerisque luctus gravida a nisl. 
                                mollis nibh ac ipsum cubilia fusce habitant consectetur metus, praesent phasellus duis nam sed condimentum sed, 
                                nisi sollicitudin himenaeos elementum viverra nulla taciti. 
                                lectus morbi lacinia libero molestie nostra habitant ipsum risus, pellentesque ultricies duis sollicitudin nostra tempus mollis nibh aliquam, 
                                himenaeos porttitor pulvinar aptent in sociosqu massa. 
                            </p>
                            <p>
                                At conubia sollicitudin at pretium nullam porta in augue sem vivamus sed fusce, maecenas ultrices augue cras lorem cras ultricies maecenas duis arcu. 
                                curabitur orci venenatis a semper curabitur, ut arcu condimentum quam praesent, semper magna laoreet interdum. 
                                senectus vel porttitor auctor cursus diam consequat ornare pharetra feugiat, donec nulla eleifend nec potenti iaculis ultricies donec, 
                                diam aenean ac orci quisque venenatis vestibulum phasellus. 
                                sit imperdiet vulputate ligula leo sem lobortis laoreet urna, erat justo a primis dapibus quisque conubia primis, 
                                luctus eget consequat volutpat ad lobortis etiam dui, accumsan mauris malesuada vehicula blandit magna taciti. 
                            </p>
                        </article>
                    </div>
                    <Footer></Footer>
                </React.Fragment> 
                : ''
            }
        </section>
    )
}

export default withRouter(Place)