import React,{useState,useEffect} from 'react'
import {Link} from 'react-router-dom'
import './Home.css'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faArrowUp } from '@fortawesome/free-solid-svg-icons'
import {Footer} from '../../components/footer/Footer'
import placesHelper from '../../helper/places'

export const Home = () => {
    const [places,setPlaces] = useState(placesHelper)
    const [isLoaded, setIsLoaded] = useState(false)

    useEffect(() => {
        window.scrollTo(0, 0);
        setIsLoaded(true)
    },[])

    return(
        <section className="l-full-size smooth-transition">
            <div className ="m-center-row home-background">
                <header className={`m-text-center ${isLoaded ? 'text-fall' : ''}`}>
                    <h1>Venha viajar conosco</h1>
                    <p className="text-hidden">Aqui a aventura e segurança andam lado a lado</p>
                </header>
            </div>
            <div className="m-center places white-color" id="places">
                <div className="m-grid">
                    {places ? places.map((place) => {
                        return <div className="m-place">
                                    <img src={place.img} width = '100%' height="100%"></img>
                                    <div className="info">
                                        <FontAwesomeIcon className="icon" icon = {faArrowUp}></FontAwesomeIcon>
                                        <ul>
                                            <li>{place.name}</li>
                                            <li><Link to={`/place/${place.id}`}>Ver Mais</Link></li>
                                        </ul>
                                    </div>
                              </div>
                    }) : ''}
                </div>
            </div>
            <div className='m-center about white-color' id="about">
                <div class="m-slider-wrapper">
                    Travel é
                    <div class="m-slider">
                        <div className="m-wrap-text slide">
                            <div class="m-slider-text"><span>AVENTURA</span></div>
                            <div class="m-slider-text"><span>DIVERSÃO</span></div>
                            <div class="m-slider-text"><span>EMOÇÃO</span></div>
                            <div class="m-slider-text"><span>AVENTURA</span></div>
                        </div>
                    </div>
                 </div>
            </div>
            <Footer></Footer>
        </section>
    )
}